// src/components/Navbar.js
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import { motion } from 'framer-motion';
import { Menu, X } from 'lucide-react';
import logo from './logo.png'; // 


const Navbar = () => {
  const [isOpen, setIsOpen] = useState(false);

  return (
    <nav className="bg-white shadow-lg">
      <div className="max-w-7xl mx-auto px-4 sm:px-6 lg:px-8">
        <div className="flex justify-between h-16">
          <div className="flex items-center">
            <Link to="/" className="flex-shrink-0">
              <img className="h-8 w-auto" src={logo} alt="Logo" />
            </Link>
            <div className="hidden md:block ml-10">
              <div className="flex items-baseline space-x-4">
                <NavLink to="/"></NavLink>
                <NavLink to="/about"></NavLink>
                <NavLink to="/reports"></NavLink>
                <NavLink to="/events"></NavLink>
                <NavLink to="/resources"></NavLink>
                <NavLink to="/contact"></NavLink>
              </div>
            </div>
          </div>
          <div className="md:hidden flex items-center">
            <button
              onClick={() => setIsOpen(!isOpen)}
              className="inline-flex items-center justify-center p-2 rounded-md text-gray-400 hover:text-gray-500 hover:bg-gray-100 focus:outline-none focus:ring-2 focus:ring-inset focus:ring-indigo-500"
            >
              {isOpen ? <X className="block h-6 w-6" /> : <Menu className="block h-6 w-6" />}
            </button>
          </div>
        </div>
      </div>

      {isOpen && (
        <motion.div 
          initial={{ opacity: 0, y: -10 }}
          animate={{ opacity: 1, y: 0 }}
          exit={{ opacity: 0, y: -10 }}
          transition={{ duration: 0.2 }}
          className="md:hidden"
        >
          <div className="px-2 pt-2 pb-3 space-y-1 sm:px-3">
            <MobileNavLink to="/"></MobileNavLink>
            <MobileNavLink to="/about"></MobileNavLink>
            <MobileNavLink to="/reports"></MobileNavLink>
            <MobileNavLink to="/events"></MobileNavLink>
            <MobileNavLink to="/resources"></MobileNavLink>
            <MobileNavLink to="/contact"></MobileNavLink>
          </div>
        </motion.div>
      )}
    </nav>
  );
};

const NavLink = ({ to, children }) => (
  <Link
    to={to}
    className="text-gray-700 hover:bg-purple-100 hover:text-purple-900 px-3 py-2 rounded-md text-sm font-medium transition duration-300"
  >
    {children}
  </Link>
);

const MobileNavLink = ({ to, children }) => (
  <Link
    to={to}
    className="text-gray-700 hover:bg-purple-100 hover:text-purple-900 block px-3 py-2 rounded-md text-base font-medium transition duration-300"
  >
    {children}
  </Link>
);

export default Navbar;