import React, { useState } from 'react';
import { motion, AnimatePresence } from 'framer-motion';
import { Send, MessageSquare, MessageCircle, Info } from 'lucide-react';

const ContactPage = () => {
  const [formData, setFormData] = useState({
    suggestion: '',
    feedback: '',
    name: '',
    email: '',
    relatedDepartment: '',
    topic: '',
  });
  const [isSubmitting, setIsSubmitting] = useState(false);
  const [submitSuccess, setSubmitSuccess] = useState(false);
  const [showSuggestionBox, setShowSuggestionBox] = useState(false);
  const [showOtherFeedback, setShowOtherFeedback] = useState(false);

  const handleChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    setIsSubmitting(true);

    // 
    setTimeout(() => {
      setIsSubmitting(false);
      setSubmitSuccess(true);
      console.log('Form submitted:', formData);

      // 
      setFormData({ suggestion: '', feedback: '', name: '', email: '', relatedDepartment: '', topic: '' });

      // 
      setTimeout(() => {
        setSubmitSuccess(false);
      }, 3000);
    }, 2000);
  };

  const toggleSuggestionBox = () => {
    setShowSuggestionBox(!showSuggestionBox);
  };

  const toggleOtherFeedback = () => {
    setShowOtherFeedback(!showOtherFeedback);
  };

  return (
    <motion.div 
      initial={{ opacity: 0 }}
      animate={{ opacity: 1 }}
      transition={{ duration: 0.5 }}
      className="bg-gradient-to-b from-gray-100 to-white min-h-screen py-12"
    >
      <div className="container mx-auto px-4">
        <motion.h1 
          initial={{ opacity: 0, y: -20 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5 }}
          className="text-5xl font-extrabold text-center text-purple-800 mb-8"
        >
          
        </motion.h1>

        <motion.div 
          initial={{ opacity: 0, y: 10 }}
          animate={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.5, delay: 0.2 }}
          className="bg-purple-100 border border-purple-300 p-6 rounded-lg shadow-lg mb-12"
        >
          <div className="flex items-center mb-4">
            <Info className="text-purple-600 w-8 h-8 mr-2" />
            <h2 className="text-2xl font-semibold text-purple-700"></h2>
          </div>
          <p className="text-lg text-gray-700">
            
          </p>
          <div className="mt-4">
            <h3 className="text-xl font-bold text-purple-600 mb-2"></h3>
            <ul className="list-disc list-inside text-gray-700">
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
          <div className="mt-4">
            <h3 className="text-xl font-bold text-purple-600 mb-2"></h3>
            <ul className="list-disc list-inside text-gray-700">
              <li></li>
              <li></li>
              <li></li>
            </ul>
          </div>
        </motion.div>

        <motion.div 
          className="bg-white rounded-lg shadow-xl p-8 mb-12"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.5, delay: 0.4 }}
        >
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-3xl font-bold text-purple-700"></h2>
            <button 
              onClick={toggleSuggestionBox}
              className="bg-purple-600 text-white px-4 py-2 rounded-lg font-semibold flex items-center hover:bg-purple-700 transition duration-300"
            >
              {showSuggestionBox ? '' : ''} <MessageSquare size={16} className="ml-2" />
            </button>
          </div>
          <AnimatePresence>
            {showSuggestionBox && (
              <motion.div
                initial={{ opacity: 0, height: 0 }}
                animate={{ opacity: 1, height: "auto" }}
                exit={{ opacity: 0, height: 0 }}
                transition={{ duration: 0.3 }}
              >
                <form onSubmit={handleSubmit} className="space-y-4">
                  <div className="flex flex-col">
                    <label className="text-gray-700 font-bold mb-2" htmlFor="name"></label>
                    <input
                      type="text"
                      id="name"
                      name="name"
                      value={formData.name}
                      onChange={handleChange}
                      className="p-4 border border-purple-300 rounded-lg focus:outline-none focus:border-purple-500 transition duration-300"
                      required
                    />
                  </div>
                  <div className="flex flex-col">
                    <label className="text-gray-700 font-bold mb-2" htmlFor="email"></label>
                    <input
                      type="email"
                      id="email"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                      className="p-4 border border-purple-300 rounded-lg focus:outline-none focus:border-purple-500 transition duration-300"
                      required
                    />
                  </div>
                  <div className="flex flex-col">
                    <label className="text-gray-700 font-bold mb-2" htmlFor="relatedDepartment"></label>
                    <input
                      type="text"
                      id="relatedDepartment"
                      name="relatedDepartment"
                      value={formData.relatedDepartment}
                      onChange={handleChange}
                      className="p-4 border border-purple-300 rounded-lg focus:outline-none focus:border-purple-500 transition duration-300"
                      required
                    />
                  </div>
                  <div className="flex flex-col">
                    <label className="text-gray-700 font-bold mb-2" htmlFor="topic"></label>
                    <input
                      type="text"
                      id="topic"
                      name="topic"
                      value={formData.topic}
                      onChange={handleChange}
                      className="p-4 border border-purple-300 rounded-lg focus:outline-none focus:border-purple-500 transition duration-300"
                      required
                    />
                  </div>
                  <div className="flex flex-col">
                    <label className="text-gray-700 font-bold mb-2" htmlFor="suggestion"></label>
                    <textarea
                      id="suggestion"
                      name="suggestion"
                      value={formData.suggestion}
                      onChange={handleChange}
                      rows="4"
                      className="p-4 border border-purple-300 rounded-lg focus:outline-none focus:border-purple-500 transition duration-300"
                      required
                    ></textarea>
                  </div>
                  <div className="flex justify-end">
                    <motion.button
                      type="submit"
                      whileHover={{ scale: 1.05 }}
                      whileTap={{ scale: 0.95 }}
                      disabled={isSubmitting}
                      className={`bg-purple-600 text-white px-6 py-3 rounded-lg font-semibold flex items-center transition duration-300 ${isSubmitting ? 'cursor-not-allowed opacity-50' : 'hover:bg-purple-700'}`}
                    >
                      {isSubmitting ? '...' : ''} <Send size={16} className="ml-2" />
                    </motion.button>
                  </div>
                </form>
              </motion.div>
            )}
          </AnimatePresence>
        </motion.div>

        <motion.div 
          className="bg-white rounded-lg shadow-xl p-8"
          initial={{ opacity: 0 }}
          animate={{ opacity: 1 }}
          transition={{ duration: 0.5, delay: 0.6 }}
        >
          <div className="flex justify-between items-center mb-4">
            <h2 className="text-3xl font-bold text-purple-700"></h2>
            <button 
              onClick={toggleOtherFeedback}
              className="bg-purple-600 text-white px-4 py-2 rounded-lg font-semibold flex items-center hover:bg-purple-700 transition duration-300"
            >
              {showOtherFeedback ? '' : ''} <MessageCircle size={16} className="ml-2" />
            </button>
          </div>
          <AnimatePresence>
            {showOtherFeedback && (
              <motion.div
                initial={{ opacity: 0, height: 0 }}
                animate={{ opacity: 1, height: "auto" }}
                exit={{ opacity: 0, height: 0 }}
                transition={{ duration: 0.3 }}
              >
                <form onSubmit={handleSubmit} className="space-y-4">
                  <div className="flex flex-col">
                    <label className="text-gray-700 font-bold mb-2" htmlFor="name"></label>
                    <input
                      type="text"
                      id="name"
                      name="name"
                      value={formData.name}
                      onChange={handleChange}
                      className="p-4 border border-purple-300 rounded-lg focus:outline-none focus:border-purple-500 transition duration-300"
                      required
                    />
                  </div>
                  <div className="flex flex-col">
                    <label className="text-gray-700 font-bold mb-2" htmlFor="email"></label>
                    <input
                      type="email"
                      id="email"
                      name="email"
                      value={formData.email}
                      onChange={handleChange}
                      className="p-4 border border-purple-300 rounded-lg focus:outline-none focus:border-purple-500 transition duration-300"
                      required
                    />
                  </div>
                  <div className="flex flex-col">
                    <label className="text-gray-700 font-bold mb-2" htmlFor="feedback"></label>
                    <textarea
                      id="feedback"
                      name="feedback"
                      value={formData.feedback}
                      onChange={handleChange}
                      rows="4"
                      className="p-4 border border-purple-300 rounded-lg focus:outline-none focus:border-purple-500 transition duration-300"
                      required
                    ></textarea>
                  </div>
                  <div className="flex justify-end">
                    <motion.button
                      type="submit"
                      whileHover={{ scale: 1.05 }}
                      whileTap={{ scale: 0.95 }}
                      disabled={isSubmitting}
                      className={`bg-purple-600 text-white px-6 py-3 rounded-lg font-semibold flex items-center transition duration-300 ${isSubmitting ? 'cursor-not-allowed opacity-50' : 'hover:bg-purple-700'}`}
                    >
                      {isSubmitting ? '...' : ''} <Send size={16} className="ml-2" />
                    </motion.button>
                  </div>
                </form>
              </motion.div>
            )}
          </AnimatePresence>
        </motion.div>
      </div>
    </motion.div>
  );
};

export default ContactPage;
