import React, { useEffect } from 'react';
import { motion, useViewportScroll, useTransform } from 'framer-motion';
import { Link } from 'react-router-dom';
import { ChevronRight, ArrowRight } from 'lucide-react';
import { researchReports, events, resources } from '../data/siteData';
import MessageOverlay from '../components/MessageOverlay';


const HomePage = () => {
  const { scrollYProgress } = useViewportScroll();
  const backgroundY = useTransform(scrollYProgress, [0, 1], ['0%', '50%']);

  useEffect(() => {
    document.body.style.overflowX = 'hidden';
    return () => {
      document.body.style.overflowX = 'auto';
    };
  }, []);

  return (
    <div className="bg-gray-100 overflow-hidden">
      <Hero backgroundY={backgroundY} />
      <MainContent />
    </div>
  );

};

const Hero = ({ backgroundY }) => (
  <motion.section 
    className="relative h-screen flex items-center justify-center overflow-hidden"
    initial={{ opacity: 0 }}
    animate={{ opacity: 1 }}
    transition={{ duration: 1 }}
  >
    <motion.div 
      className="absolute inset-0 z-0"
      style={{
        backgroundImage: "url('https://images.pexels.com/photos/674010/pexels-photo-674010.jpeg?auto=compress&cs=tinysrgb&dpr=2&h=750&w=1260')",
        backgroundPosition: 'center',
        backgroundSize: 'cover',
        y: backgroundY
      }}
    />
    <div className="relative z-10 text-white text-center">
      <motion.h1 
        className="text-6xl md:text-8xl font-bold mb-4 text-shadow-lg"
        initial={{ y: 50, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.8, delay: 0.2 }}
      >
        
      </motion.h1>
      <motion.p
        className="text-xl md:text-2xl mb-8 text-shadow"
        initial={{ y: 50, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.8, delay: 0.4 }}
      >
        
      </motion.p>
      <motion.div
        initial={{ y: 50, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 0.8, delay: 0.6 }}
      >
        <Link 
          to="/about" 
          className="bg-white text-purple-600 px-8 py-3 rounded-full font-semibold text-lg shadow-lg hover:bg-purple-100 transition duration-300 inline-flex items-center"
        >
           <ArrowRight className="ml-2" />
        </Link>
      </motion.div>
    </div>
  </motion.section>
);

const MainContent = () => (
  <main className="container mx-auto px-4 py-16">
    <LatestReports />
    <UpcomingEvents />
    <HumanRightsResources />
  </main>
);

const LatestReports = () => (
  <section className="mb-32">
    <h2 className="text-4xl md:text-5xl font-bold mb-12 text-gray-800 text-center"></h2>
    <div className="grid grid-cols-1 md:grid-cols-2 gap-12">
      {researchReports.slice(0, 2).map((report, index) => (
        <motion.div
          key={report.id}
          initial={{ opacity: 0, x: index % 2 === 0 ? -50 : 50 }}
          whileInView={{ opacity: 1, x: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.8 }}
          className="group"
        >
          <div className="relative overflow-hidden rounded-3xl shadow-xl">
            <img 
              src={report.image} 
              alt={report.title} 
              className="w-full h-80 object-cover transition duration-300 transform group-hover:scale-110"
            />
            <div className="absolute inset-0 bg-gradient-to-t from-black to-transparent opacity-70" />
            <div className="absolute bottom-0 left-0 p-6 text-white">
              <h3 className="text-2xl font-bold mb-2">{report.title}</h3>
              <p className="mb-4 opacity-90">{report.excerpt}</p>
              <Link 
                to={report.link} 
                className="inline-flex items-center text-white hover:text-purple-300 transition duration-300"
              >
                 <ChevronRight size={16} className="ml-1" />
              </Link>
            </div>
          </div>
        </motion.div>
      ))}
    </div>
  </section>
);

const UpcomingEvents = () => (
  <section className="mb-32">
    <h2 className="text-4xl md:text-5xl font-bold mb-12 text-gray-800 text-center"></h2>
    <div className="space-y-16">
      {events.slice(0, 2).map((event, index) => (
        <motion.div
          key={event.id}
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.8 }}
          className="flex flex-col md:flex-row items-center bg-white rounded-3xl shadow-xl overflow-hidden"
        >
          <div className="md:w-1/2">
            <img src={event.image} alt={event.title} className="w-full h-64 md:h-full object-cover" />
          </div>
          <div className="md:w-1/2 p-8">
            <h3 className="text-2xl font-bold mb-4 text-gray-800">{event.title}</h3>
            <p className="text-gray-600 mb-4">{event.description}</p>
            <p className="text-purple-600 font-semibold mb-6">{event.date}</p>
            <Link 
              to={event.link} 
              className="inline-flex items-center bg-purple-600 text-white px-6 py-2 rounded-full hover:bg-purple-700 transition duration-300"
            >
               <ArrowRight size={16} className="ml-2" />
            </Link>
          </div>
        </motion.div>
      ))}
    </div>
  </section>
);

const HumanRightsResources = () => (
  <section className="mb-32">
    <h2 className="text-4xl md:text-5xl font-bold mb-12 text-gray-800 text-center"></h2>
    <div className="grid grid-cols-1 md:grid-cols-3 gap-8">
      {resources.slice(0, 3).map((resource, index) => (
        <motion.div
          key={resource.id}
          initial={{ opacity: 0, y: 50 }}
          whileInView={{ opacity: 1, y: 0 }}
          viewport={{ once: true }}
          transition={{ duration: 0.8, delay: index * 0.1 }}
          className="bg-white rounded-3xl shadow-xl p-8 hover:shadow-2xl transition duration-300"
        >
          <div className="text-purple-600 mb-6">
            <resource.icon size={48} />
          </div>
          <h3 className="text-2xl font-bold mb-4 text-gray-800">{resource.title}</h3>
          <p className="text-gray-600 mb-6">{resource.description}</p>
          <Link 
            to={resource.link} 
            className="inline-flex items-center text-purple-600 hover:text-purple-800 transition duration-300"
          >
             <ArrowRight size={16} className="ml-1" />
          </Link>
        </motion.div>
      ))}
    </div>
  </section>
  
);

export default HomePage;